.card {
  background: #fff;
  border-radius: 5px;
  height: 180px;
  margin: 1em;
  position: relative;
  width: 205px;
  -webkit-box-shadow: 0 5px 6px #999, 0 5px 6px #999;
          box-shadow: 0 5px 6px #999, 0 5px 6px #999;
  text-align: right;
}

.card > .img-container {
  height: 100%;
  /* overflow: hidden; */
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 100%;
  height: 100%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}


.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}


